.container {
  padding: 2vw;
  border: 1px solid #ccc;
  border-radius: 5px;
}

input[type="text"] {
  cursor: pointer;
  color: black;
  padding: 0.5rem 1rem;
  /* color: transparent; */
  /* text-shadow: 0 0 0 #2196f3; */
  border: none;
  outline: none;
  background-color: #f2f2f2;
  border-radius: 3px;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  width: 100%;
}
textarea[type="text"] {
  cursor: pointer;
  color: black;
  padding: 0.5rem 1rem;
  /* color: transparent; */
  /* text-shadow: 0 0 0 #2196f3; */
  border: none;
  outline: none;
  background-color: #f2f2f2;
  border-radius: 3px;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  width: 100%;
}

@media (min-width: 768px) {
  textarea {
    columns: 1;
  }
  input[type="text"] {
    width: 80%;
  }
}

#Tulathumb {
  cursor: pointer;
  color: black;
}

button {
  padding: 0.5rem 1rem;
  background-color: #2196f3;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
}

button:hover {
  background-color: #0c7cd5;
}

.custom-alert {
  position: fixed;
  top: 2vw;
  right: 2vw;
  background-color: #4caf50;
  color: #fff;
  border-radius: 4px;
  padding: 1rem;
  display: flex;
  align-items: center;
  animation: custom-alert-animation 0.5s ease-in-out;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
}

.custom-alert i {
  margin-right: 1rem;
}

.custom-alert-text {
  font-weight: bold;
  color: #fff;
}
.scroll-buttons-container {
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  z-index: 9999;
}

.scroll-button {
  display: block;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.scroll-button:hover {
  background-color: #555;
}

textarea {
  
  caret-color: transparent;
  width: 320px;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  background-color: #f2f2f2;
  border: none;
  outline: none;
  border-radius: 3px;
  font-family: "Open Sans", sans-serif;
}
@media (min-width: 900px){
  .container{
      max-width: 1270px;
  }
   textarea{
    
  width:1230px;
  caret-color: transparent;
  font-size: 1.2rem;
  padding: 0.5rem 0.5rem;
  background-color: #f2f2f2;
  border: none;
  outline: none;
  border-radius: 3px;
  font-family: "Open Sans", sans-serif;
  }
}
@keyframes custom-alert-animation {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
